footer{
    background-color: $dark;
    padding: 50px 0;
    color:$light;
    font-size:16px;
    font-family:'hb';
    @include maxsm {
        padding: 20px 0;
        text-align: center
    }
    a{
        text-decoration:none;
        color:$light;
    }
    ul{padding:0;margin:0;list-style-type:none;}
    li{
        display:inline-block;
    }
    .links{
        li{
            border-right: 1px solid #616161;
            padding:0 8px 0 5px;
            &:last-child{border-right:none;}
            &:first-child{padding-left: 0;}
        }
    }
    .left{
        span{
            display:block;
            padding-top:15px;
        }
    }
    .social{
        @include maxsm{margin-top: 20px;}
        text-align:right;
        @include maxsm{
            text-align:center;
         }
        li{margin:0 10px;
            &:last-child{
                margin-right:0;
            }
        }
        a{
            font-size:16px;
            display:block;
            text-decoration:none;
        }
    }
    @include sm{
        .left{float: left;}
        .right{float: right;}
    }


    .store{
        display:block;
        text-align:right;
        padding-top:15px;
         @include maxsm{
            text-align:center;
         }
        a{
            text-decoration:none;
            margin-bottom: 15px;
            display:inline-block;
        }
    }
}