body {
  font-family: 'h';
  font-size: 14px;
  color: #000;
}

.dt {
  display: table;
  height: 100%;
  width: 100%;
}

.dtc {
  display: table-cell;
  height: 100%;
  width: 100%;
  vertical-align: middle;
}

.mar15 {
  margin: 15px 0;
}

@media (max-width: 767px) {
  .table-responsive {
    border: none;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

@media (min-width: 1270px) {
  .container {
    width: 1230px;
  }
}

nav {
  background-color: #161c22;
}

nav .logo {
  padding: 1px 0 0;
}

@media (max-width: 767px) {
  nav .logo img {
    max-width: 74%;
  }
}

@media (max-width: 479px) {
  nav .logo {
    padding: 12px 0;
    width: 85px;
  }
  nav .logo img {
    max-width: 80%;
  }
}

@media (max-width: 767px) {
  nav .menu-toggle {
    padding: 10px 0;
  }
}

@media (max-width: 767px) {
  nav > .container {
    position: relative;
  }
}

.i-user {
  width: 41px;
  height: 41px;
  display: block;
  border-radius: 50%;
  overflow: hidden;
}

@media (max-width: 479px) {
  .i-user {
    width: 31px;
    height: 31px;
  }
}

.i-user img {
  vertical-align: top;
  width: 100%;
  height: 100%;
}

.menu-toggle {
  color: #fff;
  font-size: 28px;
}

.main-menu {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  display: block;
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s ease-out;
  transform: translateY(-100%);
}

.main-menu.bg-light {
  background-color: rgba(255, 255, 255, 0.8);
}

.main-menu.active {
  transform: translateY(0);
  visibility: visible;
  opacity: 1;
}

.main-menu .menu-container {
  display: table;
  width: 100%;
  height: 100%;
  text-align: center;
  position: relative;
}

.main-menu ul {
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.main-menu ul li {
  display: block;
}

.main-menu ul a {
  color: #27a6b6;
  font-size: 43px;
  font-family: 'ab';
  text-decoration: none;
  line-height: 85px;
  text-transform: capitalize;
}

.main-menu ul a:focus, .main-menu ul a:active, .main-menu ul a:hover {
  color: #fd604b;
}

.main-menu ul a.active {
  color: #fd604b;
  font-weight: bold;
}

@media (max-width: 479px) {
  .main-menu ul a {
    font-size: 18px;
    line-height: 3;
  }
}

@media (max-width: 767px) {
  .main-menu ul a {
    font-size: 22px;
    line-height: 2;
  }
}

.main-menu .menu-rocket {
  position: absolute;
  transition: all 0.5s;
}

@media (max-width: 767px) {
  .main-menu .menu-rocket {
    max-width: 100px;
  }
}

.main-menu .menu-close {
  font-size: 40px;
  position: absolute;
  right: 50px;
  top: 50px;
  text-decoration: none;
  color: #565b5f;
}

@media (max-width: 479px) {
  .main-menu .menu-close {
    top: 20px;
    right: 20px;
  }
}

.nav-bar {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.nav-bar li > a {
  text-decoration: none;
  color: #fff;
  font-size: 30px;
  position: relative;
  padding: 27px 0 27px 25px;
  display: block;
}

.nav-bar li > a:hover {
  color: #ff5f44;
}

@media (max-width: 767px) {
  .nav-bar li > a {
    font-size: 18px;
    padding: 8px 0;
  }
}

.nav-bar li {
  float: left;
}

@media (max-width: 767px) {
  .nav-bar li {
    padding-left: 10px;
  }
  .nav-bar li:first-child {
    padding-left: 0;
  }
}

